/******************************************************************

Stylesheet: Main Stylesheet

Here's where the magic happens. Here is where you import
all of your Sass files so they can compile into one
CSS file.

******************************************************************/

// Import custom mixins
@import "mixins";

// Import local Foundation overrides - Don't link to Bower version as it will be overwritten whenever updated
@import "settings";

// Import foundation.scss from vendor files
@import "../../vendor/foundation-sites/scss/foundation";

// Import Motion-UI from vendor files
@import "../../vendor/motion-ui/src/motion-ui";
@include motion-ui-transitions;
@include motion-ui-animations;

// Select the components you want to use
  @include foundation-global-styles;
  @include foundation-grid;
  // @include foundation-flex-grid;
  // @include foundation-flex-classes;
  @include foundation-typography;
  @include foundation-forms;
  @include foundation-button;
  @include foundation-accordion;
  @include foundation-accordion-menu;
  @include foundation-badge;
  @include foundation-breadcrumbs;
  @include foundation-button-group;
  @include foundation-callout;
  @include foundation-card;
  @include foundation-close-button;
  @include foundation-menu;
  @include foundation-menu-icon;
  @include foundation-drilldown-menu;
  @include foundation-dropdown;
  @include foundation-dropdown-menu;
  @include foundation-responsive-embed;
  @include foundation-label;
  @include foundation-media-object;
  @include foundation-off-canvas;
  @include foundation-orbit;
  @include foundation-pagination;
  @include foundation-progress-bar;
  @include foundation-slider;
  @include foundation-sticky;
  @include foundation-reveal;
  @include foundation-switch;
  @include foundation-table;
  @include foundation-tabs;
  @include foundation-thumbnail;
  @include foundation-title-bar;
  @include foundation-tooltip;
  @include foundation-top-bar;
  @include foundation-visibility-classes;
  @include foundation-float-classes;



// Import custom styles (Load before main, base, and editor)
@import "fonts";
@import "variables";

// Import your custom styles
@import "main";
@import "base";
@import "sidebar";
@import "navigation";
@import "footer";
@import "galleries";
@import "pagination";
@import "buttons";
@import "header";
@import "reveal";
@import "tabs";
@import "iefixes";
@import "overrides";
@import "editor";
@import "flexslider";
@import "lightgallery";
@import "lg-transitions";
@import "animate";
@import "fluidbox";
@import "rrssb";
@import "effects";
@import "aos";
@import "development";



// Import your retina/HD styles
@import "2x";

// Import your print styles
@import "print";
